.intro {
  // background-repeat: no-repeat;
  // background-position: top center;
  // background-size: 128%;
  &-top {
    background-image: url(../img/intro/intro-top.png);
    background-repeat: no-repeat;
    background-position: top center;
    background-size: cover;
    padding-bottom: 180px;
  }
  &__bottom {
    position: relative;
    &::before {
      content: "";
      position: absolute;
      background-image: url(../img/intro/intro-bottom.png);
      background-repeat: no-repeat;
      background-position: top center;
      background-size: cover;
      top: -65%;
      width: 100%;
      height: 260%;
      z-index: -1;
      @include tablet {
        height: 137%;
    }
      @include mobile-sm {
        height: 126%;
    }
    
    }
  }
  &__human {
    position: absolute;
    bottom: -71px;
    right: 0;
    user-select: none;
    @include tablet {
        display: none;
    }
  }
  &-human-2 {
    display: none;
    @include tablet {
        display: block;
        margin: 0 auto;
    }
  }
  &__top {
    margin-top: 60px;
    @include mobile-sm {
        margin-top: 0;
    }
  }
  &__title {
    max-width: 535px;
    font-family: "Montserrat-Bold";
    font-weight: bold;
    font-size: 46px;
    line-height: 50px;
    margin: 0 0 20px;
    color: #fff;
    @include mobile-sm {
        font-size: 32px;
line-height: 34px;
    }
    span {
        color: $red;
    }
}
&__subtitle {
    font-size: 22px;
    line-height: 32px;
    color: #fff;
    margin: 0 0 32px;
    max-width: 570px;
    @include mobile-sm {
        font-size: 18px;
line-height: 24px;
    }
    
  }
  &__teasers {
    display: flex;
    align-items: center;
    @include mobile-sm {
        flex-direction: column;
        align-items: flex-start;
    }
  }
  &__btns {
    display: flex;
    align-items: center;
    margin-top: 50px;
    @include mobile-sm {
        flex-direction: column;
        align-items: flex-start;
        margin-top: 30px;
    }
    
  }

  &__btn {
    margin-right: 30px;
    @include mobile-sm {
        margin-right: 0;
        margin-bottom: 20px;
    }
  }

  &-bottom__certificates {
    margin-top: 10px;
    margin-bottom: 40px;
  }

  &__container {
    position: relative;
    top: -30px;
    @include tablet {
        top: 20px;
    }
  }

  &__logo.logo-rounded {
    position: absolute;
    top: -27%;
    left: 50%;
    transform: translateX(-50%);
    @include tablet {
        top: -125px;
    }
    @include mobile-sm {
        top: -145px;
    }
  }

  &-bottom {
    &__term-text {
      font-size: 22px;
      line-height: 32px;
      margin: 0;
      @include mobile-sm {
        font-size: 18px;
line-height: 26px;
        }
    }
    &__term {
        font-family: "Montserrat-Bold", sans-serif;
        font-weight: bold;
        font-size: 42px;
        line-height: 50px;
        color: $red;
        @include mobile-sm {
            font-size: 36px;
            line-height: 50px;
          }
      
    }
    &__text {
      max-width: 570px;
      font-size: 16px;
      line-height: 26px;
      margin: 17px 0 0;
      @include mobile-sm {
        font-size: 14px;
line-height: 22px;
      }
    }
  }
}

.teaser {
  display: flex;
  align-items: center;
  &:hover &__icon {
    background-color: $blur-hover;
  }
  &__icon {
    width: 70px;
    height: 70px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: $blur;
    transition: background-color 0.3s ease-in;
  }
  svg {
    width: 40px;
    height: 40px;
  }
  &__text {
    font-size: 16px;
    line-height: 18px;
    max-width: 185px;
    margin-left: 30px;
    color: #fff;
  }
  + .teaser {
    margin-left: 15px;
    @include mobile-sm {
        margin-left: 0;
        margin-top: 10px;
    }
  }
}

.logo-rounded {
  width: 70px;
  height: 70px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  background-color: #fff;
  position: relative;
  &__icon {
    width: 28px;
    height: 28px;
  }
  &__text {
    position: absolute;
    width: 142px;
    height: 142px;
    z-index: 1;
    animation: rotate 10s infinite;
    animation-timing-function: linear;
  }
}

@keyframes rotate {
  0% {
    transform: rotate(0);
  }
  100% {
    transform: rotate(360deg);
  }
}
